body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#background-image{
  position:absolute;
  top:0;
  left:0;
  width:100%;
  height:100%;
  z-index:0;
}

.weather-icon-container{
  height:150px;
  width:150px;
  display:flex;
  justify-content: center;
  align-items: center;
  overflow: visible;
  margin-bottom: -20px;
}

.weather-icon{
  transform:scale(3)
}

.superscript{
  font-size: .6em;
  vertical-align: top;
}

.temperature-container{
  font-size:4em;
  padding-left: 15px;
  display: flex;
}

.temperature-switch{
  display:flex;
  align-items: center;
  padding:10px;
  font-size:.6em;
}

.temperature-unit{
  transform:scale(.4);
  transition:transform .4s linear;
  cursor:pointer;
}
.temperature-unit:hover{
  transform: scale(.5);
}
.temperature-unit-active{
  transform:scale(1.2);
  transition:transform .3s linear;
}

.search-container{
  -webkit-animation:fadein .4s linear 0s 1 normal forwards;
          animation:fadein .4s linear 0s 1 normal forwards;
  height: 100px;
}
.search-form{
  display:flex;
  border-radius: 10px;
  height:50px;
}

.search-bar{
  font-size: 1.5em;
  box-sizing:border-box;
  padding:10px;
  outline:none;
  border-radius: 0;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  border:none;
}
.search-button{
  height:100%;
  width:50px;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  outline:none;
  border:none;
  cursor:pointer;
  background-color: lightgray;
}
#search-error{
  color:red;
  -webkit-animation:fadein .4s linear 0s 1 normal forwards;
          animation:fadein .4s linear 0s 1 normal forwards;
}
#search-spinner{
  width:100%;
  margin-top: 15px;
  -webkit-animation: spin 1s ease-in-out 0s infinite alternate;
          animation: spin 1s ease-in-out 0s infinite alternate;
  text-align: center;
  font-size: 3em;
}
@-webkit-keyframes spin {
    from {transform:rotate(0deg);}
    to {transform:rotate(360deg);}
}
@keyframes spin {
    from {transform:rotate(0deg);}
    to {transform:rotate(360deg);}
}
@-webkit-keyframes fadeinout {
  0%{
    opacity:0;
    transform:translateY(20px);
  }
  5%{
    opacity:1;
    transform:translateY(0px);
  }
  95%{
    opacity:1;
    transform:translateY(0px);
  }
  100%{
    opacity:0;
    transform:translateY(20px);
  }
}
@keyframes fadeinout {
  0%{
    opacity:0;
    transform:translateY(20px);
  }
  5%{
    opacity:1;
    transform:translateY(0px);
  }
  95%{
    opacity:1;
    transform:translateY(0px);
  }
  100%{
    opacity:0;
    transform:translateY(20px);
  }
}

@media only screen and (max-width: 768px) {
  .search-bar{
    width:200px;
  }
}

.weather-panel{
  display:flex;
  flex-direction: column;
  align-items: center;
  -webkit-animation:fadein .4s linear 0s 1 normal forwards;
          animation:fadein .4s linear 0s 1 normal forwards;
}
.weather-search-button{
  font-size:2em;
  cursor:pointer;
  margin-top: 20px;
}
.weather-location{
  font-size:1.3em;
}

#about-container{
  position:absolute;
  width:100%;
  height:100%;
  z-index:1;
  display:flex;
  justify-content: center;
}
#about-close{
  margin-top:15px;
  margin-right: -25px;
  margin-bottom: -25px;
  font-size:2em;
  color:red;
  align-self: flex-end;
}
#about-close:hover{
  cursor:pointer;
}

#about-opaque-background{
  position:absolute;
  width:100%;
  height:100%;
  opacity: .6;
  background-color: black;
}

#about-panel{
  position:relative;
  display:flex;
  flex-direction: column;
  margin-top: 85px;
  height:calc(100% - 100px);
  width: 75%;
  max-width:650px;
  padding-left:40px;
  padding-right:40px;
  background-color: #282828;
  border-radius: 10px;
  opacity: 1;
  z-index:2;
  overflow-y:auto;
}

#about-container a{
  color:rgb(173, 216, 230);
}
#about-container a:visited{
  color:rgb(220, 50, 220);
}

@media only screen and (max-width: 768px) {
  #about-panel{
    height:calc(100% - 85px);
  }
}
@media only screen and (max-width: 400px) {
  #about-panel{
    overflow-y:auto;
  }
}

#footer{
  font-size:4em;
  width:100%;
  display:flex;
  justify-content: flex-end;
}
.footer-link{
  color:white;
  text-decoration: none;
  margin-right: 20px;
  margin-left: 20px;
  cursor:pointer;
}
@media only screen and (max-width: 768px) {
  #footer{
    font-size:3em;
    justify-content: center;
  }
}

.main {
  position:absolute;
  width:100vw;
  height:100vh;
  background-color:black;
  overflow: hidden;
  color:white;
}

.app{
  position:absolute;
  width:100%;
  height:100%;
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

}
.app-header{
  text-align:center;
}

.transitioning{
  -webkit-animation:fadeout .4s linear 0s 1 normal forwards;
          animation:fadeout .4s linear 0s 1 normal forwards;
}
@-webkit-keyframes fadein{
  0%{
    opacity:0;
    transform:translateY(-20px);
  }
  100%{
    opacity:1;
    transform:translateY(0px);
  }
}
@keyframes fadein{
  0%{
    opacity:0;
    transform:translateY(-20px);
  }
  100%{
    opacity:1;
    transform:translateY(0px);
  }
}
@-webkit-keyframes fadeout{
  0%{
    opacity:1;
    transform:translateY(0px);
  }
  100%{
    opacity:0;
    transform:translateY(-20px);
  }
}
@keyframes fadeout{
  0%{
    opacity:1;
    transform:translateY(0px);
  }
  100%{
    opacity:0;
    transform:translateY(-20px);
  }
}

